html {
  height: 100%;
}

body {
  margin: 0;
  min-height: 100%;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  color: #4a4a4a;
  background-color: #E5E5E5;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

button {
  font-weight: 600;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 0.5px;
}

.visually-hidden {
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  border: none;
  position: absolute;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.body-wrapper {
  margin: 0 auto;
  padding: 50px 103px;
  min-height: 100%;
  width: 960px;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}

.page-header {
  margin-bottom: 44px;
}
.page-header__wrapper {
  min-height: 65px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.page-header__logo {
  border-radius: 50%;
  background-color: #2196F3;
  box-shadow: 0 9px 11px 0 rgba(55, 104, 142, 0.25);
}

.main-container {
  display: grid;
  grid-template-columns: 232px 502px;
  column-gap: 18px;
  box-sizing: border-box;
}

.transfer-form__fieldset {
  margin: 0;
  padding: 20px;
  padding-top: 22px;
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  border: none;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}
.transfer-form__title {
  margin: 0;
  font-size: 12px;
  line-height: 1;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}
.transfer-form__checkbox-label {
  display: grid;
  grid-template-columns: 20px 1fr;
  column-gap: 10px;
  cursor: pointer;
}
.transfer-form__checkbox {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.transfer-form__checkbox:checked + .transfer-form__checkbox-mark {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='12' height='8' viewBox='0 0 12 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M4.28571 8L0 4.16123L1.20857 3.07869L4.28571 5.82726L10.7914 0L12 1.09021L4.28571 8Z' fill='%232196F3'/%3e%3c/svg%3e ");
  background-repeat: no-repeat;
  background-position: 50%;
}
.transfer-form__checkbox:hover + .transfer-form__checkbox-mark {
  border-color: #1963a0;
}
.transfer-form__checkbox:disabled + .transfer-form__checkbox-mark, .transfer-form__checkbox:disabled ~ .transfer-form__checkbox-name {
  opacity: 0.3;
}
.transfer-form__checkbox:focus + .transfer-form__checkbox-mark {
  outline: 1px solid #1963a0;
}
.transfer-form__checkbox-mark {
  width: 20px;
  height: 20px;
  border: 1px solid #2196F3;
  border-radius: 2px;
  box-sizing: border-box;
}
.transfer-form__checkbox-name {
  font-size: 13px;
  line-height: 20px;
  font-weight: 400;
}

.filter-list {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  min-height: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-flow: dense;
  overflow: hidden;
  align-items: top;
  box-sizing: border-box;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  text-align: center;
  list-style: none;
}
.filter-list__link {
  color: #4A4A4A;
  text-decoration: none;
}
.filter-list__item {
  padding: 15px;
  flex-grow: 1;
  border: 1px solid #DFE5EC;
  box-sizing: border-box;
  background-color: #fff;
  cursor: pointer;
}
.filter-list__item--current {
  background-color: #2196F3;
  cursor: default;
}
.filter-list__item:not(.filter-list__item--current):hover {
  box-shadow: inset 0 1px 20px 1px rgba(0, 0, 0, 0.1);
}
.filter-list__item:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.filter-list__item:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.filter-list__item--current .filter-list__link {
  cursor: default;
  color: #fff;
}

.results {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
}

.offer {
  padding: 20px;
  padding-top: 17px;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  cursor: pointer;
}
.offer__header {
  margin-bottom: 18px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.offer__price {
  margin: 0;
  font-size: 24px;
  line-height: 1;
  color: #2196F3;
}
.offer__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  list-style: none;
}
.offer__list-item {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 20px;
  align-items: baseline;
}

.offer-cell__header {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
  color: #A0B0B9;
  text-transform: uppercase;
}
.offer-cell__value {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.showmore-button {
  padding: 15px 20px;
  width: 100%;
  font-size: 12px;
  line-height: 20px;
  text-transform: uppercase;
  color: #fff;
  background-color: #2196F3;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.showmore-button:hover {
  box-shadow: inset 0 1px 20px 1px rgba(0, 0, 0, 0.1);
}